.product-list {
    width: 100%;

    .top {
        width: 100%;
        border-bottom: 2px solid #ccc;
        display: flex;
        justify-content: space-between;
        align-items: center;

        // flex-wrap: wrap;
        .icons {
            display: flex;
            justify-content: center;
            align-items: center;

            &>* {
                margin-right: 7px;
                cursor: pointer;
            }
        }

        .sort {
            label {
                font-size: 1.4rem;
                font-weight: 500;
                margin: 0 5px;
            }

            select {
                font-size: 1.6rem;
                font-weight: 300;
                border: none;
                // border-bottom: 1px solid #777;
                outline: none;
            }
        }
    }

