    //   flex: 1;

    //   .icon {
    //     position: absolute;
    //     top: 50%;
    //     left: 1rem;
    //     transform: translateY(-50%);
    //   }

    //   input[type="text"] {
    //     display: block;
    //     font-size: 1.6rem;
    //     font-weight: 300;
    //     padding: 1rem;
    //     padding-left: 3rem;
