    //     width: 100%;
    //     border: 1px solid #777;
    //     border-radius: 3px;
    //     outline: none;
    //   }
    // }
    @media screen and (max-width: 800px) {
        .top {
            flex-direction: column;
            align-items: start;
            padding-bottom: 5px;
        }

        .search {
            width: 100%;
        }
    }
}

.grid {
    display: flex;
    justify-content: space-around;

    flex-wrap: wrap;
